import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import axios from "axios";
import Layout from "./../components/Layout/Layout";

const ProductDetails = () => {
  const [product, setProduct] = useState({});
  const [relatedProduct, setRelatedProduct] = useState([]);

  const params = useParams();
  const navigate = useNavigate();

  //Get Single Product
  useEffect(() => {
    const getProduct = async () => {
      try {
        const { data } = await axios.get(
          `/api/v1/product/single-product/${params.slug}`
        );

        setProduct(data?.product);
        getSimilarProduct(data?.product._id, data?.product.category._id);
      } catch (error) {
        console.log(error);
      }
    };
    if (params?.slug) getProduct();
  }, [params.slug]);

  //Get Similar Product
  const getSimilarProduct = async (pid, cid) => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/related-product/${pid}/${cid}`
      );

      setRelatedProduct(data?.product);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Layout>
      {/* {JSON.stringify(product, null, 4)} */}
      <div className="row container mt-3">
        <div className="col-md-6">
          <img
            src={product._id && `/api/v1/product/single-picture/${product._id}`}
            alt={product.name}
            className="img img-responsive"
            height="300"
          />
        </div>

        <div className="col-md-6 ">
          <h1 className="text-center">Product Details</h1>
          <h6>Name : {product.name}</h6>
          <h6>Description : {product.description}</h6>
          <h6>Price : {product.price}</h6>
          <h6>Category : {product?.category?.name}</h6>
          <button className="btn btn-secondary ms-1">ADD TO CART</button>
        </div>
      </div>

      <hr />

      <div className="row container">
        <h6>Similar Products</h6>
        {relatedProduct.length < 1 && (
          <p className="text-center">No Similar Product Found</p>
        )}

        <div className="d-flex flex-wrap">
          {relatedProduct?.map((product) => (
            <div
              key={product._id}
              className="card m-2"
              style={{ width: "18rem" }}
            >
              <img
                className="card-img-top"
                alt={product.name}
                src={
                  product._id && `/api/v1/product/single-picture/${product._id}`
                }
              />
              <div className="card-body">
                <h5 className="card-title">{product.name}</h5>
                <p className="card-text">
                  {product.description.substring(0, 30)}...
                </p>
                <p className="card-text"> $ {product.price}</p>
                <button
                  className="btn btn-primary ms-1"
                  onClick={() => navigate(`/product/${product.slug}`)}
                >
                  More Details
                </button>
                <button className="btn btn-secondary ms-1">ADD TO CART</button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </Layout>
  );
};

export default ProductDetails;
